<template>
		<swiper :options="swiperOption" :not-next-tick="notNextTick" ref="mySwiper" class="banner">
			<!-- slides -->
			<swiper-slide v-for="(item,i) in items" :key="i"><img class="banner-img" :src="item" /></swiper-slide>
			<!-- Optional controls -->
			<div class="swiper-pagination" slot="pagination"></div>
			<!--<div class="swiper-button-prev" slot="button-prev"></div>
			<div class="swiper-button-next" slot="button-next"></div>
		  <div class="swiper-scrollbar" slot="scrollbar"></div>-->
		</swiper>

</template>

<script>
	import { swiper, swiperSlide } from 'vue-awesome-swiper'
	require('swiper/dist/css/swiper.css')
	
	export default {
		name: 'carrousel',
		data() {
			return {
				items:['/static/img/hxzise.png','/static/img/zzxiaochi.png','/static/img/d5niupai.png'],
				// notNextTicknotNextTicktrueNextTickswiperswiperswipertrue
				notNextTick: true,
				swiperOption: {
					// swiper options swiperapi
					autoplay: 3000,
					speed:300,
					loop:true,
					direction: 'horizontal',
					grabCursor: true,
					autoplayDisableOnInteraction:false,
					setWrapperSize: true,
					//autoHeight: true,
					pagination: '.swiper-pagination',
					paginationClickable: true,
					//prevButton: '.swiper-button-prev',
					//nextButton: '.swiper-button-next',
					//scrollbar: '.swiper-scrollbar',
					mousewheelControl: true,
					observeParents: true,
					// debugger
					//debugger: true,
					// swiperswiper
					onTransitionStart(swiper) {
						//console.log(swiper)
					}
					// more Swiper configs and callbacks...
					// ...
				}
			}
		},
		// swiperswipernotNextTicktrue
		computed: {
			swiper() {
				return this.$refs.mySwiper.swiper
			}
		},
		components: {
	    swiper,
	    swiperSlide
	  },
		mounted() {
			// swiper
			this.swiper.slideTo(3, 1000, false)
		}
	}
</script>
<style lang="css" scoped="scoped">
	.swiper-pagination-bullet {
    background: #e5e5e5;
    opacity: 0.8;
    }
    .swiper-pagination-bullet-active {
	    opacity: 1;
	    background: #007aff;
		}
</style>


