<template lang="html">
    <div id="fulldialog-conid-1" class="fdialog-content">
     <div class="fdialog-rcon">
      <!--@author pooky@lagou.com-->
      <template v-for="(city, i) in cities">
        <div class="cities-header">
          {{city.nameStr}}
        </div>
        <table class="cities-list">
          <thead>
            <tr>
              <th></th>
              <th></th>
              <th></th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="j in Math.ceil(city.cityList.length/3)" class="cities-list-item">
              <td @click="gotoSearchPage(city.cityList[3*(j-1) + (k-1)])" v-for="k in 3" class="activeable cities-item" :data-item="city.cityList[3*(j-1) + (k-1)]">{{city.cityList[3*(j-1)+(k-1)]}}</td>
            </tr>
          </tbody>
        </table>
      </template>

     </div>
    </div>
   </body>
  </html>
</template>

<script>
export default {
  data() {
    return {
      cities: [{"cityList":["","","","","",""],"name":"","nameStr":""},{"cityList":["","","","","","","","","","","","","","","","","","",""],"name":"","nameStr":"ABCDEF"},{"cityList":["","","","","","","","","","","","","","","","","","","","","",""],"name":"","nameStr":"GHIJ"},{"cityList":["","","","","","","","","","","","","","","","","","","","",""],"name":"","nameStr":"KLMN"},{"cityList":["","","","","","","","",""],"name":"","nameStr":"OPQR"},{"cityList":["","","","","","","","","","","","","","",""],"name":"","nameStr":"STUV"},{"cityList":["","","","","","","","","","","","","","","","","","","","","","","","","","","","","","","",""],"name":"","nameStr":"WXYZ"}]
    }
  },
  methods: {
    gotoSearchPage(cityName) {
      this.$router.push({name: 'search', query: {cityName: cityName}})
    }
  },
  mounted() {
    console.log(this.$store.state.tabIndex);
  }
}
</script>

<style lang="css" scoped>
#fulldialog-conid-1 {
  height: 100%;
  overflow: scroll;
}
.fdialog-container {
	display: none;
	position: fixed;
	left: 0;
	top: 0;
	width: 100%;
	height: 100%
}

.fdialog-container .fdialog-header {
	height: 45px;
	line-height: 45px;
	background-color: #00b38a;
	color: #fff;
	font-size: 1.8rem;
	text-align: center;
	position: relative
}

.fdialog-container .fdialog-header .left {
	position: absolute;
	left: 0;
	top: 0;
	height: 100%;
	width: 40px
}

.fdialog-container .fdialog-header .left:active {
	background-color: #f0f0f0
}

.fdialog-container .fdialog-header .left .corner {
	width: 9px;
	height: 20px;
	margin: 13px auto 0 auto;
	background: url(/images/mobile/asset/common/img/icon2.png ) no-repeat -1.5px -20.5px;
	background-size: 250px 250px;
	display: inline-block
}

.fdialog-container .fdialog-header .right {
	position: absolute;
	right: 0;
	top: 0;
	height: 100%;
	width: 40px
}

.fdialog-container .fdialog-header .right:active {
	background-color: #f0f0f0
}

.fdialog-container .fdialog-header .right .corner {
	width: 17px;
	height: 20px;
	margin: 13px auto 0 auto;
	background: url(/images/mobile/asset/common/img/icon2.png ) no-repeat -60px -46.5px;
	background-size: 250px 250px;
	display: inline-block
}

.fdialog-container .fdialog-header .right-text {
	position: absolute;
	right: 15px;
	top: 0;
	height: 100%
}

.fdialog-container .fdialog-header .right-text .text {
	font-size: .14rem;
	color: #fff
}

.fdialog-container .fdialog-content {
	height: 100%;
	background-color: #fff;
	overflow: scroll;
	-webkit-overflow-scrolling: touch;
	-moz-overflow-scrolling: touch;
	overflow-scrolling: touch
}

.fdialog-container .fdialog-rcon {
	margin-bottom: 45px;
	padding-bottom: 45px
}

.cities-header {
	border-bottom: 1px solid #e8e8e8;
	height: 25px;
	line-height: 25px;
	padding: 0 15px;
	font-size: .12rem;
	color: #888
}

.selected {
	background-color: #e7f3f0
}

.cities-list {
	width: 100%;
	border-bottom: 1px solid #e8e8e8
}

.cities-item {
	height: 60px;
	line-height: 60px;
	text-align: center;
	font-size: .15rem;
	color: #333;
	min-width: 100px
}

.cities-item:active {
	background-color: #f0f0f0
}
</style>



